 control, etc. inherentes a la producción porcícola.")])])])])],1),_c('div',{staticClass:"col-5"},[_c('LineaTiempoD',{staticClass:"color-primario"},[_c('div',{staticClass:"row",attrs:{"numero":"4","titulo":"Control de medicamentos"}},[_c('div',{staticClass:"col"},[_c('p',{staticClass:"text-small"},[_vm._v("En la producción de porcinos todos los medicamentos veterinarios, vacunas, alimentos balanceados, plaguicidas y suplementos, deben tener registro ICA, ser almacenados y direccionados en un cuarto especial y bajo las condiciones que estos requieren, separando los medicamentos, plaguicidas y los productos alimenticios en unidades de almacenamiento diferentes.")])])]),_c('div',{staticClass:"row",attrs:{"numero":"5","titulo":"Bienestar animal"}},[_c('div',{staticClass:"col"},[_c('p',{staticClass:"text-small"},[_vm._v("Las prácticas de manejo, las condiciones de la infraestructura, los elementos y las condiciones de la producción deben ir dirigidas a que los animales conserven en lo posible los rasgos etológicos naturales, velando porque reciban sus raciones y agua en cantidades y condiciones apropiadas.")])])]),_c('div',{staticClass:"row",attrs:{"numero":"6","titulo":"Personal"}},[_c('div',{staticClass:"col"},[_c('p',{staticClass:"text-small"},[_vm._v("El recurso humano es fundamental en cualquier producción y se requiere que esté capacitada y se dedique en sus labores para que esta sea exitosa, por lo cual las condiciones laborales deben beneficiar a los empleados, además debe instruirse y capacitarse periódicamente a los operarios y demás personal con el fin de que estén actualizados y puedan desarrollar lo más idóneamente posible sus labores, lo cual se refleja positivamente en la producción.")])])])])],1)])])]),_c('p',{staticClass:"mt-5",attrs:{"data-aos":"zoom-in"}},[_vm._v("En la siguiente figura se describen los componentes temáticos necesarios para llevar a cabo una correcta implementación de las buenas prácticas ganaderas en la unidad productiva porcina.")]),